.b-footer__contacts a::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 3px;
  top: 100%;
  background-color: #FFCB05;
}
@media (min-width: 1200px) {
  .b-footer__contacts a::after {
    width: calc(100% - 10px);
    height: 4px;
    top: calc(100% - 5px);
  }
}

