body {
  @include B18;

  @media (min-width:768px) {
    @include B1;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: calc($col_padding / 2);
  padding-right: calc($col_padding / 2);

  @media (min-width:768px) {
    padding-right: 0;
    padding-left: 0;
    max-width: 704px;
  }

  @media (min-width:1200px) {
    max-width: 1327px;
  }
}

.g-wrapper {
  max-width: 100vw;

  margin-top: 64px;
  margin-bottom: 64px;

  @media (min-width:768px) {
    margin-top: 50px;
    margin-bottom: 80px;
  }

  @media (min-width:1200px) {
    margin-top: 80px;
    margin-bottom: 80px;
  }
}

.w-100 {
  width: 100% !important;
}

.img-responsive {
  width: 100%;
  height: auto;
}

.mb-1 {
  margin-bottom: $col_padding !important;
}

.mb--1 {
  margin-bottom: -$col_padding !important;
}

[data-hidden-input] {
  display: none;

  &.active {
    display: block;
  }
}

.d-none {
  display: none !important;
}